<script>
import axios from "axios";

export default {
  name: "Feature",
  data() {
    return {
      results: null,
      errors: [],
      featuretitle: 'Blade Runner',
      
    };
  },
  methods: {
    loadfeature: function() {
      axios
        .get("https://api.datamuse.com/words", {
          params: {
            t: this.featuretitle
          }
        })
        .then(response => {
          this.results = response.data;
        })
        .catch(error => {
          this.errors.push(error);
        });
    }
  }
};
